<template><undefined>
<h1>TreeSelect 树选择</h1>
<p>树型选择控件。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>类似 Select 的选择控件，可选择的数据结构是一个树形结构时，可以使用 TreeSelect，例如公司层级、学科系统、分类目录等等。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-tree-select :data=\&quot;treeData1\&quot; allow-clear style=\&quot;width:300px\&quot; @select=\&quot;onSelect\&quot; @clear=\&quot;onClear\&quot;&gt;&lt;/v-tree-select&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n      treeData1: [{\n        title: 'parent 1',\n        expanded: true,\n        children: [{\n          title: 'parent 1-0',\n          expanded: true,\n          children: [{\n            title: 'my leaf',\n          }, {\n            title: 'your leaf',\n          }]\n        }, {\n          title: 'parent 1-1',\n          children: [{\n            title: \&quot;&lt;span style='color: #08c'&gt;sss&lt;/span&gt;\&quot;\n          }]\n        }]\n      }],\n    }\n  },\n  methods: {\n      onSelect(data) {\n        console.log(data);\n      },\n      onClear(data) {\n        console.log('clear', data);\n      }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-tree-select :data="treeData1" allow-clear="" style="width:300px" @select="onSelect" @clear="onClear"></v-tree-select>
</template>


</div>
