                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-tree-select</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"asyncData"</span> <span class="hljs-attr">allow-clear</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:300px"</span> <span class="hljs-attr">:async</span>=<span class="hljs-string">"getData"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tree-select</span>&gt;</span>
