<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="treeselect-props"><a class="header-anchor" href="#treeselect-props" aria-hidden="true">¶</a> TreeSelect Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>可嵌套的节点属性的数组，生成tree的数据</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>multiple</td>
<td>支持多选</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>allowClear</td>
<td>显示清除按钮</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>async</td>
<td>异步加载数据</td>
<td>Function(node)</td>
<td>-</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>Function</td>
<td>() =&gt; document.body</td>
</tr>
<tr>
<td>position</td>
<td>设置节点可拖拽</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="tree-events"><a class="header-anchor" href="#tree-events" aria-hidden="true">¶</a> Tree Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>check</td>
<td>点击复选框触发</td>
<td>childrenArray</td>
</tr>
<tr>
<td>select</td>
<td>点击树节点触发</td>
<td>childrenArray</td>
</tr>
<tr>
<td>clear</td>
<td>点击清除按钮触发</td>
<td>null</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
  data() {
    return {
      treeData1: [{
        title: 'parent 1',
        expanded: true,
        children: [{
          title: 'parent 1-0',
          expanded: true,
          children: [{
            title: 'my leaf',
          }, {
            title: 'your leaf',
          }]
        }, {
          title: 'parent 1-1',
          children: [{
            title: "<span style='color: #08c'>sss</span>"
          }]
        }]
      }],
      treeData2: [{
        title: 'parent 1',
        expanded: true,
        children: [{
          title: 'parent 1-0',
          expanded: true,
          children: [{
            title: 'my leaf',
          }, {
            title: 'your leaf',
          }, {
            title: 'self leaf',
          }]
        }, {
          title: 'parent 1-1',
          children: [{
            title: "<span style='color: #08c'>sss</span>"
          }]
        }, {
          title: 'parent 1-2',
        }]
      }],
      asyncData:  [
        { title: 'pNode 01', children: []},
        { title: 'pNode 02', children: []},
        { title: 'pNode 03'},
      ]
    }
  },
  methods: {
    onCheck(data) {
      console.log(data)
    },
    onSelect(data) {
      console.log(data);
    },
    onClear(data) {
      console.log('clear', data);
    },
    getData(node) {
      console.log(node);
      return new Promise(resolve => {
        setTimeout(()=>{
          resolve([
            { title: `leaf ${node.clue}-0`, children: []},
            { title: `leaf ${node.clue}-1`},
            { title: `leaf ${node.clue}-2`},
          ])
        }, 1000)
      })
    },
  }
}
</script>


